.color-sort-list-group-item {
    .color-picker {
        width: 30px;
        height: 22px;
    }

    .available-colors-item {
        $size: 25px;
        display: inline-block;
        height: $size;
        width: $size;
        border-radius: 50%;
        margin-right: 0.5em;
        cursor: pointer;

        &:hover, &.active {
            padding: 2px;
            background-clip: content-box;
        }

        &:hover {
            border: 1px solid #777;
        }

        &.active {
            border: 2px solid #337ab7;
        }
    }

    .available-colors-list {
        white-space: nowrap;
        overflow-x: auto;
        margin-bottom: 0;
        margin-top: .5em;
    }
}
